import {Ref, nextTick, ref} from "vue"
export default (data:Ref<DataIndex_Composition_Formula_Item_OBJ[]>) => {
  const curInputValue = ref<number | null>(null);
  const inputRef = ref();
  const curInputIndex = ref<number | null>(null);
  /**
   *
   * @param number
   */
  const handleNumberClick = (
    index: number,
    number: number | null | undefined
  ) => {
    if (number === null || number === undefined) {
      curInputValue.value = 0;
    } else {
      curInputValue.value = number;
    }
    curInputIndex.value = index;
    
    nextTick(() => {
      inputRef.value && inputRef.value?.focus();
    });
  };
  const handleInputBlur = (index: number) => {
    data.value = data.value.map(
      (it: DataIndex_Composition_Formula_Item_OBJ, i: number) => {
        if (i == index) {
          let obj = {
            ...it,
            number_value: Number(curInputValue.value),
          };
          curInputValue.value = null;
          curInputIndex.value = null;
          return obj;
        }
        return it;
      }
    )
  };
  return {
    curInputIndex,
    curInputValue,
    inputRef,
    handleInputBlur,
    handleNumberClick,
  };
};
