import { watch } from 'vue';
import {  ElMessageBox } from 'element-plus';
import type Node from 'element-plus/es/components/tree/src/model/node';
import { useStore } from 'vuex';

/**
 * 创建/更新一个分组/更新某个指标名称
 * */

export default (
  cusInfo: any,
  data: any,
  inputRef: any,
  cusInputValue: any,
  emit: any,
  inputIsLoading: any,
  status: any
) => {
  const store = useStore();
  /**
   * 监听<新建目录>的状态
   */
  watch(
    () => [store.state.dataIndex.dir_add_status, cusInfo, data],
    (val) => {
      if (cusInfo.value.length && store.state.dataIndex.dir_add_status) {
        store.commit('POP_MESSAGE', {type: 'warning', message:  '其他任务在编辑，请先取消！'})
        return;
      }
      // 如果新建状态为true，且没有正在编辑的input，就进入创建状态
      if (store.state.dataIndex.dir_add_status && !cusInfo.value.length) {
        const newChild: Tree = {
          id: null,
          name: '',
          children: [],
          is_dir: true,
        };
        cusInfo.value = [newChild]; // 标记为正在编辑
        data.value.unshift(newChild); // 插入data的第一项

        // 延迟使正在编辑的input聚焦
        setTimeout(() => {
          if (!inputRef.value) {
            cusInfo.value = [];
            cusInputValue.value = '';
          }
          inputRef.value && inputRef.value.focus();
        }, 300);
      }
      // <新建分组>状态关闭
      store.commit('dataIndex/set_dir_add_status', false);
    }
  );
  /**
   * 点击<创建指标>触发事件
   * @param node
   * @param _data
   * @param e
   * @param is_dir
   * @returns
   */
  const append = (node: Node, _data: Tree, is_dir: boolean) => {
    // 展开该项的所在目录
    node.expanded = true;
    store.commit('dataIndex/set_expandIDs', {
      isOpen: true,
      id: _data.id,
    });
    if (cusInfo.value.length) {
      // 有正在编辑的
      store.commit('POP_MESSAGE', {type: 'warning', message:  '其他任务在编辑，请先取消！'})
      return;
    }
    cusInfo.value = [node, _data];
    const newChild: Tree = {
      id: null,
      name: '',
      children: [],
      is_dir,
    };
    if (!_data.children) {
      _data.children = [];
    }
    _data.children.unshift(newChild);
    // 延迟使正在编辑的input聚焦
    setTimeout(() => {
      setTimeout(() => {
        inputRef.value.focus();
      }, 0);
    }, 100);
  };

  /**
   * 点击<重命名>触发事件
   * @param node
   * @param _data
   */
  const handleEdit = (node: Node, _data: Tree) => {
    status.value = 'edit';
    cusInfo.value = [node, { ..._data }];
    cusInputValue.value = _data.name;
    _data.name = '';// input展示
    setTimeout(() => {
      inputRef.value && inputRef.value.focus();
    }, 100);
  };

  return {
    append,
    handleEdit,
  };
};
