import { formulaInfojy } from '@/utils/common';
import { number } from 'echarts';
import { watch, ref } from 'vue';
import { useStore } from 'vuex';

export default () => {
  const store = useStore();
  const data = ref<DataIndex_Composition_Formula_Item_OBJ[]>([]);
  const checkFormulaArr = ref<any[]>([]);
  const cur_time = ref<string>('');
  /**
   * 如果公式中没有指标，将当前统计周期粒度设为空
   */
  watch(data, () => {
    if (!data.value.filter((item) => item.type === 'index').length) {
      cur_time.value = '';
    }
  });
  /**
   * 左侧拖拽结束
   */
  watch(
    () => [store.state.dataIndex.drag_item_data],
    () => {
      if (!store.state.dataIndex.drag_item_data) {
        return;
      }
      handleFormulaAndFilterAdd(
        store.state.dataIndex.drag_item_data.fromData?.id,
        store.state.dataIndex.drag_item_data.fromData,
        store.state.dataIndex.drag_item_data.newIndex,
        'drag'
      );
    }
  );
  /**
   * 向 条件和公式 添加一项指标 之后的操作
   * @param text1
   * @param text2
   * @param id
   * @param obj
   * @returns
   */
  const handleFormulaAndFilterAdd = (
    id: any,
    obj: Tree,
    newIndex: number,
    mode?: 'drag'
  ) => {
    if (!obj) {
      return;
    }
    // 获取详情
    store
      .dispatch('dataIndex/action_set_dir_nav_data', { id, isNotInfoTip: true })
      .then((res) => {
        if (mode === 'drag') {
          // 没有统计周期
          if (!cur_time.value) {
            cur_time.value =
              store.state.dataIndex.index_meta_data_all[id]?.meta.time;
          }
          // 有统计周期
          else {
            // 周期不一致
            if (
              cur_time.value !==
              store.state.dataIndex.index_meta_data_all[id]?.meta.time
            ) {
              store.commit('POP_MESSAGE', {
                type: 'warning',
                message: '统计周期粒度不一致',
              });
              data.value = data.value.filter((item, i) => i != newIndex);
              return;
            }
          }
          
          // 初始化周期/周期一致
          data.value = [
            ...data.value.filter((item, i) => i != newIndex),
            {
              id,
              name: obj.name,
              type: 'index',
              index_type: obj.index_type
            } as DataIndex_Composition_Formula_Item_OBJ,
          ];
        }
      })
      .catch(() => {});
  };
  /**
   *
   * @param command
   * @param direction
   * @param index
   * @param cur_index 当前的公式与条件
   */
  const handleCommand = (
    command: Formula_Custom_Type | 'delete',
    direction: 'left' | 'right',
    index: number
  ) => {
    // 删除
    if (command === 'delete') {
      data.value = data.value.filter(
        (item: DataIndex_Composition_Formula_Item_OBJ, i: number) => i !== index
      );
      return;
    }
    // 左侧--------------------------------------------------------------------------------
    if (direction === 'left') {
      data.value = [
        ...data.value.slice(0, index),
        {
          type: 'formula',
          formula: command,
        },
        ...data.value.slice(index),
      ];
      // 右侧---------------------------------------------------------------------
    } else if (direction === 'right') {
      data.value = [
        ...data.value.slice(0, index + 1),
        {
          type: 'formula',
          formula: command,
        },
        ...data.value.slice(index + 1),
      ];
    }
  };
  /**
   *
   * @param command 值
   * @param index data中的第几项
   */
  const handleFormulaCommand = (
    command: Formula_Custom_Type | 'delete' | 'number',
    index: number
  ) => {
    // 删除
    if (command === 'delete') {
      data.value = data.value.filter(
        (item: DataIndex_Composition_Formula_Item_OBJ, i: number) => i !== index
      );
    }
    // 添加数字
    else if (command === 'number') {
      data.value = [
        ...data.value.slice(0, index + 1),
        { type: 'number', number_value: 0 },
        ...data.value.slice(index + 1),
      ];
    }
    // 其他，则为修改
    else {
      data.value = data.value.map(
        (item: DataIndex_Composition_Formula_Item_OBJ, i: number) => {
          if (i === index) {
            return {
              ...item,
              formula: command,
            };
          }
          return item;
        }
      );
    }
  };

  /**
   * 公式校验
   * @returns
   */
  const checkFormula = () => {
    // let text = `(+i+i)/(i-i)-i+i)`

    let text = '';
    let i_length = 0;
    data.value.forEach((it: DataIndex_Composition_Formula_Item_OBJ) => {
      if (it.type === 'formula') {
        text += it.formula;
      } else {
        text += 'i';
        i_length++;
      }
    });
    if (i_length === 1) {
      checkFormulaArr.value = ['公式必须存在两个指标'];
      return ['公式必须存在两个指标'];
    } else if (text.includes('ii')) {
      checkFormulaArr.value = ['指标之间必须要有运算符'];
      return ['指标之间必须要有运算符'];
    } else {
      let check = formulaInfojy(text);
      if (check === true) {
        checkFormulaArr.value = ['公式正确'];
        return ['公式正确'];
      } else {
        checkFormulaArr.value = check;
        return check;
      }
    }
  };
  return {
    data,
    handleCommand,
    handleFormulaCommand,
    checkFormula,
    checkFormulaArr,
  };
};
