import { getIndexInfoById } from '@/utils/dataIndexs';
import type { FormRules, FormInstance } from 'element-plus';
import { computed, onMounted, ref, reactive } from 'vue';
import { useStore } from 'vuex';

export default () => {
  const store = useStore();
  interface RuleForm {
    index_id: any;
    parent_id: any; // 所属目录
    type: Edition_Index_Type; // 显示类型（数值、图片、表格）
  }
  const ruleFormRef = ref<FormInstance>();
  let ruleForm = reactive<RuleForm>({
    index_id: '',
    parent_id: '',
    type: 'value',
  });
  const indexInfo = ref<DataIndex_Index_Info | null>(null);
  onMounted(() => {
    let info = getIndexInfoById(store.state.dataIndex.cur_tree_index_node?.id);
    if (info) {
      indexInfo.value = info;
      ruleForm.parent_id = info.parent_id;
      ruleForm.index_id = info.id;
    }
  });
  /**
   * 验证规则
   */
  const rules = reactive({
    parent_id: [{ required: true, message: '目录不能为空', trigger: 'change' }],
    index_id: [{ required: true, message: '指标不能为空', trigger: 'change' }],
    type: [
      { required: true, message: '显示类型不能为空', trigger: 'change' },
    ],
  });
  return {
    rules,
    ruleFormRef,
    ruleForm,
    indexInfo,
  };
};
