import { ref, onMounted, onBeforeMount, watchEffect } from 'vue';
import { useStore } from 'vuex';

export default (treeRef: any, append: Function) => {
  const contextMenuRef = ref(); // 右键菜单的dom
  const contentMenuStyle = ref({});
  const contentMenuNode = ref();
  const contentMenuData = ref();
  const store = useStore();


  const handleDocumentClick = () => {
    contentMenuNode.value = null;
    contentMenuStyle.value = {
      display: 'none',
    };
  };

  onMounted(() => {
    document.addEventListener('click', handleDocumentClick);
  });
  onBeforeMount(() => {
    document.removeEventListener('click', handleContentMenuClick);
  });

  const handleContentMenuClick = () => {
    // contextmenu.value.style.display="none";
    contentMenuStyle.value = {
      display: 'none',
    };
    contentMenuNode.value = null;
  };
  const handleContentMenu = (e: any, node: any, data: any) => {
    contentMenuNode.value = node;
    contentMenuData.value = data;
    if (treeRef.value.el$.clientHeight < e.clientY - 80) {
      contentMenuStyle.value = {
        ...contentMenuStyle.value,
        display: 'block',
        top: e.clientY - 150 + 'px',
      };
    } else {
      contentMenuStyle.value = {
        display: 'block',
        left: e.clientX - 160 + 'px',
        top: e.clientY - 80 + 'px',
      };
    }
    if (treeRef.value.el$.clientWidth < e.clientX - 80) {
      contentMenuStyle.value = {
        ...contentMenuStyle.value,
        display: 'block',
        left: e.clientX -  250 + 'px',
      };
    } else {
      contentMenuStyle.value = {
        display: 'block',
        left: e.clientX - 160 + 'px',
        top: e.clientY - 80 + 'px',
      };
    }

    return false; //返回一个错误的指使，禁止菜单弹出
  };

  return {
    contentMenuData,
    contentMenuNode,
    contentMenuStyle,
    contextMenuRef,
    handleContentMenu,
    handleContentMenuClick,
  };
};
