import {ElMessageBox} from 'element-plus';
import { useStore } from 'vuex';
import { inject, ref } from 'vue';
import type Node from 'element-plus/es/components/tree/src/model/node';

export default () => {
  const store = useStore();
  const isModel = ref(false);
  const modelNeedInfo = ref<any>({node: {}, _data: {}})
  // 删除指标/分组前的提示
  const beforeRemove = (node: Node, _data: Tree) => {
    if (_data.children?.length) {
      // 不能删除
      store.commit('POP_MESSAGE', {type: 'error', message:  `<${_data.name}>分组下有指标，不允许删除！`})
      return;
    }
    isModel.value = true;
    modelNeedInfo.value = {
      node,
      _data,
      text: _data.name
    }
    // cusInfo.value = [node, _data];
    
  };
  //删除节点
  const remove = (node: Node, _data: Tree) => {
    const parent = node.parent;
    const children: Tree[] = parent.data.children || parent.data;
    const index = children.findIndex((d) => d.id === _data.id);
    children.splice(index, 1);
    // data.value = [...data.value];
  };
  const handleModelOK = () => {
    isModel.value = false;
    store
      .dispatch('dataIndex/action_set_dirs', {
        status: 'delete',
        data: {
          id: modelNeedInfo.value._data.id,
        },
      })
      .then((res:any) => {
        // remove(node, _data);
      })
      .catch((err) => {
        console.log('err:', err);
        
      });
  };
  const handleModelCancel = () => {
    isModel.value = false;
  };

  return {
    beforeRemove,
    remove,
    isModel,
    handleModelOK,
    handleModelCancel,
    modelNeedInfo
  };
};
