import type Node from 'element-plus/es/components/tree/src/model/node';
import { ElMessageBox } from 'element-plus';
import { ref, watch, inject, onMounted, nextTick } from 'vue';
import { getPathText } from '@/utils/dataIndexs';
import { useStore } from 'vuex';
import { useRoute } from 'vue-router';
export default (
  cusInputValue: any,
  cusInfo: any,
  status: any,
  inputIsLoading: any,
  remove: Function
) => {
  // blur
  const store = useStore();
  const route = useRoute();

  /**
   * 清空
   */
  const clear = () => {
    cusInfo.value = [];
    cusInputValue.value = '';
    status.value = '';
  }
  /**
   * 失去焦点
   * @param node 
   * @param _data 
   * @returns 
   */
  const handleNodeBlur = (node: Node, _data: Tree) => {
    if (!cusInputValue.value && status.value === 'edit') {
      // 是编辑状态，但是input里面没有值，清空
      clear();
      return;
    } else if (
      status.value === 'edit' &&
      cusInputValue.value === cusInfo.value[1].name
    ) {
      // 是编辑状态，值没有改变，将保存的值重置会库里面，清空，并将值恢复（因为input的展示是_data.name='',不为空则不展示input）
      _data.name = cusInfo.value[1].name;
      clear();
    }

    if (!cusInputValue.value && cusInfo.value.length) {
      // input里面没值，但是cursInfo有值，清空
      remove(node, _data);// 添加状态，取消添加，将该节点删除
      clear();
    }
  };

  /**
   * 如果输入框有值，失去焦点，则成功发送请求
   * @param node 
   * @param _data 
   * @returns 
   */
  const handleNodeChange = (node: Node, _data: Tree) => {
    
    if (!cusInputValue.value && status.value === 'edit') {
      // 编辑状态，值为空
      store.commit('POP_MESSAGE', {type: 'warning', message:  '值不能为空！'})
      _data.name = cusInfo.value[1].name;// 值恢复
      clear();// 清空
      return;
    }
    if (!cusInputValue.value) {
      // 添加状态，若没有值，说明取消添加，则删除该节点
      remove(node, _data);
      return;
    }
    if (!_data.children) {
      _data.children = [];
    }
    // 发送请求，创建--------
    
    let path = getPathText(node, '', false, false)// 获取改节点目录名称
    /**
     * 名称不可重复----------------------------------------------------------
     */
    if(_data.is_dir && store.state.dataIndex.index_dirs.children.map((it:any)=>it.name).includes( cusInputValue.value.trim())) {
      // 操作目录 
      // 名称已存在
      store.commit('POP_MESSAGE', {type: 'error', message: '名称已存在！'})
      if(status.value === 'edit') {
        _data.name =  cusInfo.value[1].name// 如果是编辑状态，则值恢复
      }else {
        remove(node, _data);// 如果是添加状态，则删除该节点
      }
      clear();// 清空
      return;
    }else if(!_data.is_dir) {
      // 操作指标
      // 指标不能重复
      const data =  store.getters['dataIndex/all_index_flat_dirs'].map((it:any)=>it.name);
      if(data.includes(cusInputValue.value.trim())) {
        // 重复了
        store.commit('POP_MESSAGE', {type: 'error', message: '名称已存在！'})
        if(status.value === 'edit') {
          _data.name =  cusInfo.value[1].name// 如果是编辑状态，则值恢复
        }else {
          remove(node, _data);// 如果是添加状态，则删除该节点
        }
        clear();// 清空
        return;
      }
    }
    /**
     * 走到这里，说明名称没有重复
     * 更新名称-----------------------------------------------------------
     */
    if(status.value === 'edit') {
      // 更新目录名称
      store
        .dispatch('dataIndex/action_set_dirs', {
          status: 'update',
          id: _data.id,
          data: {
            index_name: cusInputValue.value.trim(),
            dir_name: _data.is_dir?cusInputValue.value.trim():path,
            is_dir: _data.is_dir?1:0,
            // children: _data.children,
            // id: status.value === 'edit' ? _data.id : null,
            meta: {},
            index_type: store.state.dataIndex.dir_status
          },
        })
        .then((res:any) => {
          clear();
        })
        .catch((err) => {
          // 修改失败
          console.log("err:", err);
          _data.name = cusInfo.value[1].name;
          clear();
        });
      // };

    } else {
      // 添加分组/指标-------------------------------------------------------------
      store
        .dispatch('dataIndex/action_set_dirs', {
          status: 'set',
          data: {
            index_name: cusInputValue.value.trim(),
            dir_name: _data.is_dir?cusInputValue.value.trim():path,
            is_dir: _data.is_dir?1:0,
            // children: _data.children,
            // id: status.value === 'edit' ? _data.id : null,
            // meta: {},
            index_type: store.state.dataIndex.dir_status,
            meta: {},
          },
        })
        .then((res:any) => {
          clear();
        })
        .catch((err) => {
          // 创建失败
          console.log("err:", err);
          remove(node, _data);
          clear();
        });
      };

    }
    

  return {
    handleNodeBlur,
    handleNodeChange,
  };
};
